  background-color: #cd4d31;
  justify-content: center;
  align-items: center;
  height: 60px;
  font-family: Raleway;
  font-size: 16px;
  font-weight: 600;
  line-height: 16px;
  transition: all .4s;
  display: flex;
}

.primary-button:hover {
  background-color: #af3419;
}

.primary-button:active {
  background-color: #ea340d;
}

@media (width >= 1280px) {
  .primary-button {
    width: 270px;
  }
}

.secondary-button {
  color: #cd4d31;
  box-sizing: border-box;
  text-align: center;
  border: 1px solid #cd4d31;
  place-content: center;
  align-items: center;
  gap: 8px;
  height: 60px;
  font-family: Raleway;
  font-size: 16px;
  font-weight: 600;
  line-height: 16px;
  transition: all .4s;
  display: flex;
}

.secondary-button:hover {
  color: #af3419;
  border: 1px solid #af3419;
}

.secondary-button:active {
  color: #ea340d;
  border: 1px solid #ea340d;
}

